.ContactEdit{
    .ContactEditForm{
        display: flex;
        width: 100%;
    }
    .ContactPreview ,
    .EditForm{
      width: 50%;
    }
    .EditForm{
        padding:50px;
        & div{
            width: 100%;
        }
        .EditFormBtn{
            padding: 20px;
        }
    }
    
}
@media (max-width: $break-mobail) {
    .ContactEdit {
      .ContactEditForm {
        flex-direction: column;
        // align-items: center;
        & > .ContactPreview {
          width: 100%;
        }
        div.EditForm {
          width: 100%;
        }
      }
    }
  }