.App-footer{
    
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: #282c34;
    display: flex;
    height: 120px;
    align-items: center;
    justify-content: space-around;
    color: white;
    p{
        margin: 2px;
    }
    h4{
      margin: 2px;
      display: inline-block;
    }
    h4:not(:last-child){
        margin-right: 20px;
    }
    .InIcon{
      display: inline-block;
      margin-right: 7px;
      font-size: 20px;
      margin-top: 5px;
      
  
    }
  }