.HomePage{
    .DataUserArea{
        background-image: url(../../assets/img/pexels-photo-572056.jpeg);
        background-size: cover;
        height: 82vh;
        display: flex;
        text-align: start;
        // align-items: center;
        flex-direction: column;
        // justify-content: center;
        background-position: center;
       
        .ContentDataUaer{
            padding: 15px;
            width: fit-content;
            margin: 10px;
            border-radius: 7px;
            color: white;
            background-color: rgba(40, 44, 52, 0.74);
            div :last-child{
                margin-bottom: 15px;
            }
            h4{
                display: inline-block;
                margin: 2px;
                font-family: Montserrat-Bold;
            }
            h3{
                margin: 2px;
                margin-bottom: 15px;
                font-family: Montserrat-Bold;
                text-transform: capitalize;
            }
        }
    }
}