.SignupPage{
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(rgba(0, 0, 0, 0.5))
    ),
    url(../../assets/img/pexels-photo-572056.jpeg);
    .SignupArea{
        background-color: rgba(255, 255, 255, 0.918);
        width: 300px;
        height: 300px;
        border-radius: 7px;
    }
    h2{
        font-family: Montserrat-Bold;
    }
    .SignupControl{
        margin-bottom: 20px;

    }
    
   
}