.ContactDetails {
  .BackLink {
    margin-bottom: 20px;
  }
  .ContactPreview{
    min-height: 400px;
  }
  .ContactPreviewTransfer {
    display: flex;
    width: 100%;

    .ContactPreview,
    .TransferFund {
      width: 50%;
    }
   
  }
}
@media (max-width: $break-mobail) {
  .ContactDetails {
    .ContactPreviewTransfer {
      flex-direction: column;
      // align-items: center;
      & > .ContactPreview {
        width: 100%;
      }
      div.TransferFund {
        width: 100%;
      }
    }
  }
}
